"use client"
import { Button } from "@/components/ui/button";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Badge } from "@/components/ui/badge";
import { DollarSign, ArrowLeft } from "lucide-react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";

const CURRENCIES = ["RWF", "USD", "EUR", "GBP"];
const FUNDING_TYPES = [
  "Grant",
  "Equity Investment",
  "Loan",
  "Convertible Note",
  "In-kind Support"
];
const DURATION_OPTIONS = [
  "One-time",
  "3 Months",
  "6 Months",
  "1 Year",
  "2 Years",
  "3+ Years"
];

type ProjectData = {
  id: string;
  title: string;
  amount: number;
  university: string;
};

type FundingFormData = {
  amount: number;
  currency: string;
  fundingType: string;
  duration: string;
  conditions: string;
  contactEmail: string;
  notes: string;
};

export function ProvideFunds() {
  const router = useRouter();
//   const { id } = router.query;
  const [project, setProject] = useState<ProjectData | null>(null);
  const [loading, setLoading] = useState(true);
  const [formData, setFormData] = useState<FundingFormData>({
    amount: 0,
    currency: "RWF",
    fundingType: FUNDING_TYPES[0],
    duration: DURATION_OPTIONS[0],
    conditions: "",
    contactEmail: "",
    notes: ""
  });

//   useEffect(() => {
//     if (id) {
//       // Simulate API fetch
//       const fetchProject = async () => {
//         setLoading(true);
//         try {
//           // Replace with actual API call
//           const mockProject = {
//             // id: id as string,
//             title: "Renewable Energy Innovation",
//             amount: 7500000,
//             university: "University of Rwanda"
//           };
//           setProject(mockProject);
//           setFormData(prev => ({ ...prev, amount: mockProject.amount }));
//         } finally {
//           setLoading(false);
//         }
//       };
//       fetchProject();
//     }
//   }, [id]);

  const handleChange = (field: keyof FundingFormData, value: string | number) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      // Replace with actual API submission
      console.log("Submitting funding:", formData);
      alert("Funding offer submitted successfully!");
    //   router.push(`/dashboard/projectPage/${id}`);
    } catch (error) {
      console.error("Submission failed:", error);
      alert("Failed to submit funding offer");
    }
  };

//   if (loading) {
//     return <div className="max-w-2xl mx-auto p-6">Loading project details...</div>;
//   }

//   if (!project) {
//     return <div className="max-w-2xl mx-auto p-6">Project not found</div>;
//   }

  return (
    <div className="max-w-2xl mx-auto p-6">
      <div className="flex items-center gap-4 mb-6">
        <Link href={`/dashboard/projectPage/`}>
          <Button variant="ghost" size="icon">
            <ArrowLeft className="h-5 w-5" />
          </Button>
        </Link>
        <h1 className="text-2xl font-bold">Provide Funding</h1>
      </div>

      <Card className="mb-6">
        <CardHeader>
          <CardTitle className="text-lg">Project Summary</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-2">
            <p className="font-medium">{project.title}</p>
            <div className="flex items-center gap-2">
              <Badge variant="outline">{project.university}</Badge>
              <Badge variant="secondary">
                <DollarSign className="h-4 w-4 mr-1" />
                {project.amount.toLocaleString()} RWF requested
              </Badge>
            </div>
          </div>
        </CardContent>
      </Card>

      <form onSubmit={handleSubmit}>
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Funding Details</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium mb-1">Amount</label>
                <Input
                  type="number"
                  value={formData.amount}
                  onChange={(e) => handleChange("amount", parseFloat(e.target.value))}
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Currency</label>
                <Select
                  value={formData.currency}
                  onValueChange={(value) => handleChange("currency", value)}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Select currency" />
                  </SelectTrigger>
                  <SelectContent>
                    {CURRENCIES.map((currency) => (
                      <SelectItem key={currency} value={currency}>
                        {currency}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium mb-1">Funding Type</label>
                <Select
                  value={formData.fundingType}
                  onValueChange={(value) => handleChange("fundingType", value)}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Select type" />
                  </SelectTrigger>
                  <SelectContent>
                    {FUNDING_TYPES.map((type) => (
                      <SelectItem key={type} value={type}>
                        {type}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Duration</label>
                <Select
                  value={formData.duration}
                  onValueChange={(value) => handleChange("duration", value)}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Select duration" />
                  </SelectTrigger>
                  <SelectContent>
                    {DURATION_OPTIONS.map((duration) => (
                      <SelectItem key={duration} value={duration}>
                        {duration}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Conditions</label>
              <Textarea
                value={formData.conditions}
                onChange={(e) => handleChange("conditions", e.target.value)}
                placeholder="Any special conditions for this funding"
              />
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Contact Email</label>
              <Input
                type="email"
                value={formData.contactEmail}
                onChange={(e) => handleChange("contactEmail", e.target.value)}
                required
                placeholder="Your contact email"
              />
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Notes</label>
              <Textarea
                value={formData.notes}
                onChange={(e) => handleChange("notes", e.target.value)}
                placeholder="Additional information for the project team"
              />
            </div>
          </CardContent>
        </Card>

        <div className="flex justify-end gap-4 mt-6">
          <Link href={`/dashboard/projectPage/${project.id}`}>
            <Button variant="outline" type="button">
              Cancel
            </Button>
          </Link>
          <Button type="submit">Submit Funding Offer</Button>
        </div>
      </form>
    </div>
  );
}